.wrapper {
  position: relative;
  width: 100%;
  height: 100vh;
  box-sizing: border-box;
  font-size: 30px;
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
  /* 字体抗锯齿渲染 */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-transition: all .4s ease;
  transition: all .4s ease;
  overflow-y: auto;
}

.main {
  width: 90%;
  min-height: calc(100vh - 50px);
  margin: 0 auto;
}

.title {
  margin: 0 auto;
  padding: 20px 0 30px 0;
  font-size: 30px;
  text-align: center;
}

.textarea_wrapper {
  width: 100%;
  overflow: hidden;
}

.textarea {
  float: left;
  width: 46%;
}

.textarea:first-child {
  margin:  0 8% 0 0;
}

.button_wrapper {
  width: 100%;
  padding: 20px 0;
  text-align: right;
}

@media (max-width: 576px) {
  .textarea {
    width: 100%;
  }
}

.ok_button {
  width: 80px;
  height: 36px !important;
  line-height: 36px !important;
  font-size: 22px !important;
  margin: 10px 0 0 20px;
}

.bottom {
  height: 50px;
  line-height: 46px;
  text-align: center;
  font-size: 20px;
}

.bottom::before {
  content: '';
  display: block;
  height: 1px;
  background: #e9e9e9;
  -webkit-transform: rotateX(45deg);
          transform: rotateX(45deg);
}

.bottom_heart {
  display: inline-block;
  width: 30px;
  font-size: 16px;
  -webkit-animation: heart_beat infinite .6s linear;
          animation: heart_beat infinite .6s linear;
}

@-webkit-keyframes heart_beat {
  20% {
    font-size: 19px;
  }
}

@keyframes heart_beat {
  20% {
    font-size: 19px;
  }
}
html, body, div, span, h1  {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  vertical-align: baseline;
}

body {
  line-height: 1;
  overflow: hidden;
}

